.App {
  text-align: center;
  font-family: 'DIGIT';
}

body {
  margin: 0;
}

.App-message {
  font-size: 3vw;
  margin: 1vw 2vw;
}

.large-digit {
  font-size: 5vw;
  width: 5.1vw;
}

.large-btn {
  width: 5vw;
  height: 5vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #b3b3b3;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
